
.c-charities-tiles {
  background-color: $s-color--background-highlight;
  max-width: 100%;

  &__outer{
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
    overflow: hidden;
  }

  &__inner {
    padding: 2rem 0 2.75rem;
  }

  &__title {
    text-align: center;
    margin-bottom: 2.4375rem;
  }

  &__tiles {
    display: grid;
    grid-auto-flow: column;
    overflow: scroll;
    scrollbar-width: none;
    scrollbar-color: $s-color--background-highlight
      $s-color--background-highlight;
    scroll-snap-type: x mandatory;

    &::-webkit-scrollbar {
      width: 0;
    }

    &::-webkit-scrollbar-track {
      background: $s-color--background-highlight;
    }

    &::-webkit-scrollbar-thumb {
      border-left: 1px solid $s-color--background-highlight;
    }
  }

  &__tile {
    min-width: 17.8125rem;
    max-width: 25rem;
    scroll-snap-align: start; 
  }

  &__tile-inner {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: min-content min-content;
    position: relative;
    padding: 0 1.25rem;
  }

  &__blob {
    grid-row: 1/2;
    grid-column: 1/-1;
    min-width: 12.5rem;
    min-height: 12.5rem;
  }

  &__content {
    grid-row: 1/2;
    grid-column: 1/-1;
    text-align: center;
    display: grid;
    align-content: center;
    padding: 1.25rem;
    grid-gap: 0.5rem;
  }

  &__charity {
    margin: 0;
    transition: all 0.5s;
  }

  &__description {
    grid-row: 2/-1;
    text-align: center;
    margin-top: 0.875rem;
    transition: all 0.5s;
  }

  &__total {
    color: $s-color--font-focus-alt;
    margin-bottom: 0;
    transition: all 0.5s;
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 6.25rem 6%;
    }

    &__title {
      margin-bottom: 3.75rem;
    }

    &__tiles {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      max-width: 100%;
    }

    &__tile-inner {
      display: block;
      position: relative;
    }

    &__tile {
      flex: 1;
      margin-bottom: 3.375rem;

      &:hover {
        .c-charities-tiles__blob path {
          stroke: #b96955;
        }

        .c-charities-tiles__total,
        .c-charities-tiles__charity {
          opacity: 0;
        }

        .c-charities-tiles__description {
          opacity: 1;
        }
      }
    }

    &__blob {
      grid-row: 1/-1;

      path {
        transition: all 0.5s;
      }
    }

    &__content {
      @include t-absolute-center-x-y();
      padding: 0;
      margin-top: 0;
      grid-gap: 1.25rem;
      width: 70%;
    }

    &__description {
      @include t-absolute-center-x-y();
      padding: 1.25rem;
      font-size: 1rem;
      opacity: 0;
      width: 76%;
      margin: 0 auto;
      padding: 0;
    }
  }

  @include t-media-query($s-large-up) {
    &__inner {
      padding: 10rem 6%;
    }

    &__title {
      margin-bottom: 3.75rem;
    }

    &__tile-inner {
      grid-template-rows: 1fr;
      align-items: center;
    }

    &__tile {
      flex: 0 0 24.996%;
      margin-bottom: 6.375rem;

      &:hover {
        .c-charities-tiles__blob path {
          stroke: #b96955;
        }

        .c-charities-tiles__total,
        .c-charities-tiles__charity {
          opacity: 0;
        }

        .c-charities-tiles__description {
          opacity: 1;
        }
      }
    }

    &__blob {
      grid-row: 1/-1;
    }

    &__content {
      grid-gap: 1.25rem;
    }

    &__description {
      grid-row: 1/-1;
      grid-column: 1/-1;
      padding: 1.25rem;
      font-size: 1rem;
      opacity: 0;
      max-width: 80%;
      margin: 0 auto;
      padding: 0;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: 14.0625rem;
      padding-bottom: 8.9375rem;
    }

    &__title {
      margin-bottom: 6.75rem;
    }
  }
}
