/*
 *  Base Colors
 *  - For base color naming conventions use: https://chir.ag/projects/name-that-color
 *  - These base colors should only ever be used inside this color file to assign to
 *    other color variables
 */
$s-color--black: #000;
$s-color--white: #FFF;
$s-color--red: red;
$s-color--clear: transparent;
$s-color--tonys-pink: #E4987F;
$s-color--janna: #F4E9D2;
$s-color--merino: #F8F3EC;
$s-color--mine-shaft: #393939;
$s-color--old-lace:#FCECDF;
$s-color--santa-fe:#B96955;
$s-color--charm:#BE7A8F;
$s-color--tacao: #EDB381;
$s-color--silver-chalice: #ADADAD;
$s-color--bay-leaf: #86AA91;

/*
 *  Color Definitions
 */

// Backgrounds
$s-color--background: $s-color--white;
$s-color--background-focus: $s-color--janna;
$s-color--background-subdued: $s-color--merino;
$s-color--background-impact: $s-color--tonys-pink;
$s-color--background-highlight: $s-color--old-lace;
$s-color--background-overlay: $s-color--mine-shaft;
$s-color--background-widget: $s-color--tacao;
$s-color--background-widget-10: rgba($s-color--background-widget, 0.1);

// Borders
$s-color--border: $s-color--black;
$s-color--border-20: rgba($s-color--border, 0.2);
$s-color--border-invert: $s-color--white;
$s-color--border-focus: $s-color--tonys-pink;
$s-color--border-clear: $s-color--clear;
$s-color--border-subdued: $s-color--mine-shaft;
$s-color--border-subdued-20: rgba($s-color--border-subdued, 0.2);
$s-color--border-subdued-alt: $s-color--merino;
$s-color--border-error: $s-color--red;
$s-color--border-progress-fill: $s-color--santa-fe;

// Buttons
$s-color--btn-font: $s-color--white;
$s-color--btn-font-alt: $s-color--black;
$s-color--btn-background: $s-color--tonys-pink;
$s-color--btn-background-alt: $s-color--white;
$s-color--btn-background-disable: $s-color--silver-chalice;
$s-color--btn-background-added: $s-color--bay-leaf;

// Fonts
$s-color--font: $s-color--black;
$s-color--font-invert: $s-color--white;
$s-color--font-subdued: $s-color--mine-shaft;
$s-color--font-subdued-20: rgba($s-color--font-subdued, 0.2);
$s-color--font-subdued-40: rgba($s-color--font-subdued, 0.4);
$s-color--font-50: rgba($s-color--black, .5);
$s-color--font-focus: $s-color--tonys-pink;
$s-color--font-focus-alt: $s-color--charm;
$s-color--font-focus-highlight: $s-color--santa-fe;

// Input
$s-color--error: $s-color--tonys-pink;

// Link
$s-color--font-link: $s-color--tonys-pink;

// Overlay
$s-color--overlay: rgba($s-color--mine-shaft, 0.4);

// Header
$s-color--header-shadow: rgba($s-color--black, 0.04);
$s-color--header-shadow-alt: rgba($s-color--black, 0.01);

// Brand colours map for button colours
$brand-colors: (
  'cream': (
    primary: #F4E9D2,
    secondary: $s-color--btn-font-alt
  ),
  'peach': (
    primary: #E4987F,
    secondary: $s-color--btn-font
  ),
  'pink': (
    primary: #F6C5D5,
    secondary: $s-color--btn-font-alt
  ),
  'black': (
    primary: #393939,
    secondary: $s-color--btn-font
  ),
  'white': (
    primary: #FFF,
    secondary: $s-color--btn-font-alt
  ),
  'yellow': (
    primary: #FDCF5E,
    secondary: $s-color--btn-font
  ),
  'violet': (
    primary: #C9B6D7,
    secondary: $s-color--btn-font
  ),
  'blue': (
    primary: #9ED4DD,
    secondary: $s-color--btn-font
  ),
  'aqua': (
    primary: #99D4CA,
    secondary: $s-color--btn-font-alt
  ),
  'deep-pink' :(
    primary: #D8758F,
    secondary: $s-color--btn-font
  ),
  'lime-green': (
    primary: #C2E0B5,
    secondary: $s-color--btn-font-alt
  ),
  'orange': (
    primary: #EDB381,
    secondary: $s-color--btn-font-alt
  )
);
